import React from 'react';

const Navbar = () => {
    const scrollToSection = (sectionId) => {
      const section = document.getElementById(sectionId);
      if (section) {
        const navbarHeight = document.getElementById('navbar').offsetHeight;
        const sectionTop = section.offsetTop - navbarHeight;
  
        window.scrollTo({
          top: sectionTop,
          behavior: 'smooth',
        });
      }
    };
  
    return (
      <nav id="navbar" className="bg-gray-800 p-4 fixed w-full top-0 z-10">
        <div className="container mx-auto flex items-center justify-between">
          <div className="text-white">Logo</div>
          <div className="flex space-x-4">
            <button onClick={() => scrollToSection('section1')} className="text-white cursor-pointer">Item 1</button>
            <button onClick={() => scrollToSection('section2')} className="text-white cursor-pointer">Item 2</button>
            <button onClick={() => scrollToSection('section3')} className="text-white cursor-pointer">Item 3</button>
          </div>
        </div>
      </nav>
    );
  };
  

export default Navbar;
